import React from "react"
import Icon from "./icon"
import { Comments, Retweet, Like, Share } from "./icons"
import DefaultProfileImage from "../../content/assets/default-profile-image.png"

import "./tweets.scss"

class Tweet extends React.Component {
  constructor(props) {
    super(props)

    this.defaultTweet = {
      name: `Name`,
      handle: `@username`,
      image: DefaultProfileImage,
      date: `Jun 11`,
      tweet: `Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam ac ex non quam fermentum sagittis sit amet eu elit. Sed ut sapien neque. Sed tincidunt pulvinar cursus. Curabitur malesuada odio sem, ut blandit magna interdum vitae. Aenean semper, sem non condimentum pellentesque sed.`
    }

    this.state = {
      tweet: props.tweet || this.defaultTweet
    }
  }

  render() {
    return (
      <div className={`tweet`}>
        <img className={`tweet-profile-image`} alt={`tweet-profile`} src={this.state.tweet.image || this.defaultTweet.image} />
  
        <span className={`tweet-content`}>
  
          <div className={`tweet-name-date`}>
            <span className={`tweet-profile-name`}>{this.state.tweet.name || this.defaultTweet.name}</span>
            <span className={`tweet-profile-handle`}>{this.state.tweet.handle || this.defaultTweet.handle}</span>
            <span className={`tweet-separator`}>{``}</span>
            <span className={`tweet-date`}>{this.state.tweet.date || this.defaultTweet.date}</span>
          </div>
  
          <div className={`tweet-text`}>{this.state.tweet.tweet || this.defaultTweet.tweet}</div>
          
          <div className={`tweet-icons`}>
            <Icon icon={Comments} type={`comments`} count={this.randBetween(100, 1000)} />
            <Icon icon={Retweet}  type={`retweet`}  count={this.randBetween(100, 1000)} />
            <Icon icon={Like}     type={`like`}     callback={() => this.likeTweet()}  />
            <Icon icon={Share}    type={`share`}    callback={() => this.shareTweet()} />
          </div>
  
        </span>
      </div>
    )
  }

  likeTweet() {
    alert('Like')
  }
  
  shareTweet() {
    alert('Share')
  }

  randBetween(low, high) {
    return Math.floor(Math.random() * (high - low + 1)) + low
  }
}


export default Tweet

